<template>
  <div id="app">
    <div id="page-header">
      <div class="main">
        <div class="t-part">
          <div class="log">
            <h1><a href="#"><img src="./assets/logo.png"></a></h1>
          </div>
          <div class="nav">
            <router-link to="/home">音乐馆</router-link>
            <router-link to="/mymusic">我的音乐</router-link>
            <router-link to="/kehuduan">客户端</router-link>
            <router-link to="/musichao">音乐号</router-link>
            <router-link to="/vip">VIP</router-link>
          </div>
          <div class="search">
            <input type="text" @focus="seachHis = !seachHis" @blur="seachHis = !seachHis" placeholder="搜索音乐、MV、歌单、用户">
            <ul v-show="seachHis">
              <li v-for="(v,i) in seachHisList" :key="i"><em>{{i+1}}</em>{{v.k}}<span>{{(v.n/10000).toFixed(1)}}W</span></li>
            </ul>
          </div>
          <div class="login">
            <a href="#">登录</a>
          </div>
          <div class="vip">
            <a href="#">开通绿钻豪华版</a>
            <a href="#">开通付费包</a>
          </div>
        </div>
        <div class="b-part">
          <ul>
            <li><a class="active" href="#">首页</a></li>
            <li><a href="#">歌手</a></li>
            <li><a href="#">专辑</a></li>
            <li><a href="#">排行榜</a></li>
            <li><a href="#">分类歌单</a></li>
            <li><a href="#">电台</a></li>
            <li><a href="#">MV</a></li>
            <li><a href="#">数字专辑</a></li>
          </ul>
        </div>
      </div>
    </div>
    
    <router-view @updatePlayBox='updatePlayBox'/>
    <div id="play-box">
      <p @click="scrollToTop">返回顶部</p>
      <p @click="playShow = !playShow"> 播放器 </p>
    </div>

    <playbox @updatePlayBox='updatePlayBox' v-show="playShow"></playbox>
    
  </div>
</template>

<script>

  import $ from "jquery"

   if(!window.hotSearchKeysmod_top_search){
      window.hotSearchKeysmod_top_search = function( info ){
        if( window.prevData === info ){
          window.prevData = false;
        }
        window.prevData = info;
      }
    }

    if(!window.getAlbumInfoCallback){
      window.getAlbumInfoCallback = function( info ){
        if( window.prevData === info ){
          window.prevData = false;
        }
        window.prevData = info;
      }
    }

    if(!window.jsonp1111){
      window.jsonp1111 = function( info ){
        if( window.prevData === info ){
          window.prevData = false;
        }
        window.prevData = info;
      }
    }

  import playbox from "./components/play.vue"

export default {
  name: 'App',
  data: function(){
    return {
      seachHis: false,
      seachHisList: [],
      playShow: false
    }
  },
  created(){

   var _that = this;
    
   $.ajax({
     url: 'https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg',
     dataType: 'JSONP',
     data: {
      g_tk: 5381,
      jsonpCallback: 'hotSearchKeysmod_top_search',
      loginUin: 0,
      hostUin: 0,
      format: 'jsonp',
      inCharset: 'utf8',
      outCharset: 'utf-8',
      notice: 0,
      platform: 'yqq',
      needNewCode: 0
     },
     complete: function(){
      if( window.prevData ){
        _that.seachHisList = window.prevData.data.hotkey.splice(0, 5);
      }
     }
   })

  },
  methods: {
    scrollToTop(){
       $("body,html").animate({scrollTop:0},500);//页面滚动到指定高度 
    },
    updatePlayBox(data){
      if( data ){
        // console.log( data )
      }
      this.playShow = !this.playShow
    }
  },
  components: {
    playbox
  }
}
</script>

<style>
html,body {
  user-select: none;
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
}
.t_cen {
  text-align: center;
}
.fl {
  float: left;
}
ul {
  list-style: none;
}
em,i {
  font-style: normal;
}
.fr {
  float: right;
}
a {
  text-decoration: none;
  color: #333;
}
.main {
  width: 1200px;
  margin: 0 auto;
}
#page-header {
  height: 142px;
}
#page-header .t-part {
  height: 90px;
  border-bottom: 1px solid #ddd;
}
#page-header .t-part .log {
  padding-right: 25px;
  padding-top: 20px;
}
#page-header .t-part > div {
  float: left;
}
#page-header .t-part .login {
  line-height: 90px;
  padding: 0 20px;
}
#page-header .t-part .nav a {
  float: left;
  height: 100%;
  padding: 0 20px;
  line-height: 90px;
  font-size: 18px;
}
#page-header .t-part .login a:hover,
#page-header .t-part .nav a:hover {
  color: #31c27c;
}
#page-header .t-part .nav a.router-link-active {
  background-color: #31c27c;
  color: #fff;
}
#page-header .t-part .search {
  padding-top: 25px;
  padding-left: 50px;
  position: relative;
}
#page-header .t-part .search ul {
  position: absolute;
  left: 50px;
  top: 62px;
  z-index: 3;
  width: 200px;
  border-radius: 3px; 
  border: 1px solid #ddd;
  background-color: #fff;
}
#page-header .t-part .search li {
  padding: 6px 0;
  font-size: 12px;
}
#page-header .t-part .search li:hover {
  background-color: #31c27c;
  color: #fff;
}
#page-header .t-part .search li:hover em,
#page-header .t-part .search li:hover span {
  color: #fff;
}
#page-header .t-part .search em {
  color: red;
  padding: 0 15px;
}
#page-header .t-part .search span {
  float: right;
  color: #888;
  padding-right: 25px;
}
#page-header .t-part .search input {
  border: none;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 0 20px 0 10px;
  outline: none;
  height: 36px;
}
#page-header .t-part .vip {
  padding-top: 25px;
}
#page-header .t-part .vip a {
  float: left;
  height: 36px;
  font-size: 12px;
  line-height: 36px;
  border: 1px solid #ddd;
  border-radius: 3px;
  padding: 0 8px;
}
#page-header .t-part .vip a:first-child {
  background-color: #31c27c;
  color: #fff;
  margin-right: 10px;
}
#page-header .b-part ul {
  width: 100%;
  text-align: center;
  line-height: 52px;
}
#page-header .b-part li {
  font-size: 0;
  display: inline-block;
}
#page-header .b-part li a {
  font-size: 16px;
  padding: 0 25px;
}
#page-header .b-part li a:hover,
#page-header .b-part li a.active {
  color: #31c27c;
}

#play-box {
  position: fixed;
  text-align: center;
  font-size: 12px;
  cursor: pointer;
  right: 50px;
  z-index: 666;
  bottom: 50px;
}
#play-box p {
  border: 1px solid #ccc;
  background-color: #fff;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 2px 1px 5px 3px rgba(0,0,0,0.5);
}
#play-box p:last-child {
  color: #fff;
  margin-top: 10px;
  background-color: #31c27c;
}

</style>



