import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import SongPlay from '@/components/songplay'
import Play from '@/components/play'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      component: Home
    },
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home/songplay/:song_id',
      component: SongPlay
    },
    {
      path: '/play',
      component: Play
    }
  ]
})



