<template>
    <div class="page-song-play">
        <div class="song-info main">
            <div class="img-box fl">
                <img :src='(songInfo && ("https://y.gtimg.cn/music/photo_new/T002R300x300M000" + songInfo.album.mid + ".jpg?max_age=2592000"))'>
            </div>
            <div class="content fl">
                <h1>{{songInfo && songInfo.album.title }}</h1>
                <p class="big">歌手：{{songInfo && songInfo.singer[0].name}}</p>
                <div class="singer">
                    <div class="left fl">
                        <p>专辑：{{songInfo && songInfo.name}}</p>
                        <p>流派：{{albumInfo && albumInfo.genre}}</p>
                        <p>发行时间：{{songInfo && songInfo.album.time_public}}</p>
                    </div>
                    <div class="right fl">
                        <p>语种：{{albumInfo && albumInfo.lan }}</p>
                        <p>发行公司：{{albumInfo && albumInfo.company
}}</p>
                    </div>
                </div>
                <ul class="dosomething">
                    <li @click="openplaybox" @dblclick="openplaybox">播放</li>
                    <li>收藏</li>
                    <li>评论({{ commentInfo && commentInfo.comment.commenttotal }})</li>
                    <li>更多</li>
                </ul>
            </div>
        </div>
        <div class="lry-box main">
            <h4>歌词</h4>
            <pre></pre>
        </div>
        <div class="commemt-box main">
            <h4>精彩评论（{{ commentInfo && commentInfo.hot_comment.commenttotal}}）</h4>
            <ul>
                <li v-for="(v,i) in (commentInfo && commentInfo.hot_comment.commentlist)" :key="i">
                    <img :src="v.avatarurl" alt="">
                    <div class="content">
                        <h5>{{v.nick}}</h5>
                        <p v-html="v.rootcommentcontent"></p>
                        <div>
                            <span>{{v.time}}</span><span>点赞数（{{v.praisenum}}）  评论</span>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
          
        
    </div>

</template>

<script>


import $ from "jquery"

export default {
    data(){
        return {
            songInfo: null,
            songLry: '',
            albumInfo: null,
            songPlayData: {isPlay: false},
            commentInfo: null
        }
    },
    beforeCreate(){

    },
    created(){
       var _that = this;
       $.ajax({
           url: 'https://c.y.qq.com/v8/fcg-bin/fcg_play_single_song.fcg',
           data: {
                songmid:  _that.$route.params.song_id,
                tpl: 'yqq_song_detail',
                format: 'jsonp',
                g_tk: 5381,
                loginUin: 0,
                hostUin: 0,
                format: 'jsonp',
                inCharset: 'utf8',
                outCharset: 'utf-8',
                notice: 0,
                platform: 'yqq',
                needNewCode: 0
           },
           dataType: 'JSONP',
           success: function( info ){
               
               _that.songInfo = info.data[0];
                _that.songPlayData.singer = _that.songInfo.singer[0].name;                          
                _that.songPlayData.name = _that.songInfo.name; 
                _that.songPlayData.id = _that.songInfo.id; 
                 _that.getAlumbInfo();

                //  console.log( _that.songInfo )

           }
       })
    },
    filters: {
        formatData(d){
            var date = new Date(d);
        }
    },
    methods: {
        openplaybox(){
            this.$store.commit("increment", this.songPlayData)
            this.$emit('updatePlayBox', this.songPlayData);
        },
        getAlumbInfo(){

            var _that = this;
            
            $.ajax({
                url: 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_album_info_cp.fcg',
                data: {
                    albummid: _that.songInfo.album.mid,
                    g_tk: 5381,
                    jsonpCallback: 'getAlbumInfoCallback',
                    loginUin: 0,
                    hostUin: 0,
                    format: 'jsonp',
                    inCharset: 'utf8',
                    outCharset: 'utf-8',
                    notice: 0,
                    platform: 'yqq',
                    needNewCode: 0,
                },
                dataType: 'JSONP',
                complete: function(){
                    if( window.prevData ){
                        _that.albumInfo = window.prevData.data;
                        // console.log( _that.albumInfo )
                    }
                }
            })

            //     000LXQlE1urOWC

            $.ajax({
                url: 'https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg',
                dataType: 'JSONP',
                data: {
                    g_tk: 5381,
                    loginUin: 0,
                    hostUin: 0,
                    format: 'json',
                    inCharset: 'utf8',
                    outCharset: 'utf-8',
                    notice: 0,
                    platform: 'yqq',
                    needNewCode: 0,
                    cid: 205361747,
                    uin: 0,
                    songmid: _that.songInfo.mid,
                    filename: ( 'C400' + _that.songInfo.file.media_mid + '.m4a' ),
                    guid: 3465117176,
                },
                success: function(info){
                    _that.songPlayData.url = 'http://dl.stream.qqmusic.qq.com/' + info.data.items[0].filename + '?vkey=' + info.data.items[0].vkey + '&guid=3465117176&uin=0&fromtag=66';
                    // console.log( _that.songPlayData )
                }
            })


            $.ajax({
                url: 'https://c.y.qq.com/base/fcgi-bin/fcg_global_comment_h5.fcg',
                dataType: 'JSONP',
                data: {
                    g_tk: 5381,
                    loginUin: 0,
                    hostUin: 0,
                    format: 'jsonp',
                    inCharset: 'utf8',
                    outCharset: 'GB2312',
                    notice: 0,
                    platform: 'yqq',
                    needNewCode: 0,
                    cid: 205360772,
                    reqtype: 2,
                    biztype: 1,
                    topid: _that.songInfo.id,
                    cmd: 8,
                    needmusiccrit: 0,
                    pagenum: 0,
                    pagesize: 25,
                    domain: 'qq.com',
                    ct: 24,
                    cv: 101010
                },
                success: function(info){
                    console.log( info )
                    _that.commentInfo = info;
                }
            })

        }
    }
}
</script>


<style>

.page-song-play {
    background-image: linear-gradient(#F2F2F2,#fff)
}
.page-song-play .song-info {
    padding-top: 50px;
    overflow: hidden;
}
.page-song-play .img-box {
    width: 250px;
    height: 250px;
    padding-right: 50px;
}
.page-song-play .img-box img {
    width: 100%;
    height: 100%;
}
.page-song-play .content h1 {
    font-size: 31px;
    line-height: 50px;
    font-weight: 400;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.page-song-play .content p.big {
    font-size: 16px;    
}
.page-song-play .content p {
    font-size: 14px;
    line-height: 33px;
}
.page-song-play .content .singer {
    overflow: hidden;
}
.page-song-play .content .singer .left {
    width: 250px;
}
.page-song-play .dosomething {
    overflow: hidden;
    padding-top: 10px;
}
.page-song-play .dosomething li {
    float: left;
    margin-right: 10px;
}
.page-song-play .dosomething li:first-child  {
    background-color: #2EC17D;
    color: #fff;
}
.page-song-play .dosomething li {
    display: block;
    width: 128px;
    height: 38px;
    font-size: 12px;
    border-radius: 3px; 
    line-height: 38px;
    text-align: center;
    border: 1px solid #ddd;
}
.page-song-play .dosomething li:hover {
    background-color: rgb(237, 236, 236); 
}

.page-song-play .lry-box {
    padding-top: 50px;
}
.page-song-play .lry-box h4 {
    padding-bottom: 20px;
}
.page-song-play .commemt-box h4 {
    width: 857px;
    font-size: 16px;
    font-weight: 400;
    line-height: 58px;
    border-bottom: 1px solid #ddd;
}
.page-song-play .commemt-box ul {
    width: 857px;
}
.page-song-play .commemt-box li {
    position: relative;
    padding: 15px 0 15px 56px;
}
.page-song-play .commemt-box img {
    width: 38px;
    height: 38px;
    position: absolute;
    border-radius: 50%;
    left: 0;
    top: 15px;
}
.page-song-play .commemt-box h5 {
    color: #999;
    font-weight: normal;
}
</style>





