<template>
   <div class="page-play">
       <div class="bg-box">
           <div class="bg-s-box">
           </div>
       </div>
       <div class="containear">
            <div class="head">
                <em class="wilte">QQ音乐</em>
                <span @click="$emit('updatePlayBox')" class="wilte">关闭</span>
                <span class="wilte">设置</span>
                <span class="wilte">登录</span>
            </div>
            <div class="song">
                <div class="list">
                    <div class="title">
                        <span>收藏</span>
                        <span>添加到</span>
                        <span>下载</span>
                        <span>删除</span>
                        <span>清空列表</span>
                    </div>
                    <ul class="content">
                        <li>
                            <input type="checkbox">
                            <div>歌曲</div>
                            <p>歌手</p>
                            <span>时长</span>
                        </li>
                        <li v-for="v in songList" :key="v.id" @click="qiehuan({url: v.url,id: v.id})">
                            <input type="checkbox">
                            <div><img src="../assets/wave.gif" v-show="v.isPlay">{{v.name}}</div>
                            <p>{{v.singer}}</p>
                            <span>时长</span>
                        </li>
                    </ul>
                </div>
                <div class="lry"></div>
            </div>
             <!--  -->
            <div class="audio-box" style="opacity: 0">
                <audio controls="controls" autoplay :src="curSong" type="audio/mp3" >
                    你的浏览器不支持播放
                </audio>
            </div>
            
       </div>
       <div id="play-btn-box">
            <i class="fl">上一首</i>
            <i @click="pushandpaly" class="fl">暂停</i>
            <i class="fl">下一首</i>
            <div class="fl kj">
                <div class="txt">
                    <span class="fl">{{whitchOne && whitchOne}}</span>
                    <span class="fr">00:49 / 05:42</span>
                </div>
                <div class="fa">
                    <div class="son"></div>
                </div>
            </div>
        </div> 
   </div>
</template>

<script>
export default {
    data: function(){
        return {
            curSong: '',
            songList: null,
            curName: ''
        }
    },
    methods: {
        qiehuan(o){
            this.curSong = o.url;
            var id = o.id;
            this.songList.forEach(function(v){
                if( v.id == id ){
                    v.isPlay = true;
                }else {
                    v.isPlay = false;
                }
            })
        },
        pushandpaly(el){
            var audio = document.querySelector("audio");
            if( audio.classList.contains('cur') ) {
                audio.classList.remove('cur');
                el.target.innerText = '暂停'
                audio.play();
            }else {
                audio.classList.add('cur');
                el.target.innerText = '播放'
                audio.pause();
            }
            
        }
    },
    watch: {
        getIsPlayIcons(v){
            if( this.$store.getters.allLists ){
                this.songList = this.$store.getters.allLists;
                this.songList.forEach(v=>{
                    v.isPlay = false;
                })
                this.songList[0].isPlay = true;
                this.curSong = this.songList[0].url;
            }
        }
    },
    computed: {
        getIsPlayIcons() {
            return this.$store.getters.isPlay;
        },
        whitchOne(){
            if( this.songList ){
                var c = this.songList.find(v=>v.isPlay)
                return c.name + ' - ' + c.singer
            }
            return ''
        }
    },
    updated(){

    }
}
</script>

<style>
 .page-play #play-btn-box {
    position: fixed;
    width: 1200px;
    left: 50%;
    height: 100px;
    margin-left: -600px;
    bottom: 20px;
} 
.page-play #play-btn-box i {
    margin-top: 10px;
    background-color: red;
    background-image: linear-gradient(rgba(0,0,0,0), rgba(0,0,0,0.4));
    border-radius: 5px;
    font-size: 12px;
    color: #fff;
    padding: 4px 10px;
    margin-left: 10px;
}
.page-play #play-btn-box .kj {
    margin-left: 50px;
    height: 100%;
    width: 450px;
}
.page-play #play-btn-box .kj .txt {
    height: 30px;
    width: 100%;
    color: #fff;
}
.page-play #play-btn-box .fa {
    width: 100%;
    height: 2px;
    position: relative;
    background-color: rgba(255,255,255,.3);
}
.page-play #play-btn-box .son {
    height: 2px;
    width: 1px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: #fff;
}
.page-play #play-btn-box .son::after {
    content: '';
    width: 10px;
    height: 10px;
    position: absolute;
    right: 0;
    top: -4px;
    border-radius: 50%;
    background-color: #fff; 
}
    .page-play {
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: 999;
        left: 0;
        top: 0;
        background-color: rgb(41, 42, 43);
    }
    .page-play .bg-box {
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.35);
        position: absolute;
        left: 0;
        top: 0;
    }
    .page-play .bg-s-box {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: 50%;
        -webkit-filter: blur(65px);
        filter: blur(65px);
        opacity: .35;
        -webkit-transform: translateZ(0);
        transform: translateZ(0);
        background-image: url(https://y.gtimg.cn/music/photo_new/T002R300x300M000002pGhNU4Md5TV.jpg?max_age=2592000);
    }
    .page-play .containear {
        width: 100%;
        height: 100%;
        position: relative;
        padding: 20px;
        box-sizing: border-box;
    }
    .page-play .head em {
        font-size: 36px;
    }
    .page-play .head span {
        float: right;
        margin-left: 10px;
    }
    .page-play .wilte {
        color: #fff;
        opacity: .3; 
        transition: all .4s;       
    }
    .page-play .wilte:hover {
        opacity: 1;
    }
    .page-play .song .title {
        overflow: hidden;
        padding-left: 100px;
        padding-top: 20px;
    }
    .page-play .song .title span {
        float: left;
        height: 38px;
        font-size: 14px;
        line-height: 38px;
        color: #fff;
        padding: 0 30px;
        margin-right: 10px;
        border: 1px solid rgba(255, 255, 255,.35);
        transition: all .4s;
    }
    .page-play .song .title span:hover {
        border: 1px solid #fff;
    }
    .page-play .song .content {
        padding-left: 100px;
        margin-top: 20px;
        width: 817px;
    }
    .page-play .song li:first-child {
        border-top: 1px solid rgba(255, 255, 255,.3);
    }
    .page-play .song li {
        border-bottom: 1px solid rgba(255, 255, 255,.3);
        height: 50px;
        line-height: 50px;
        display: flex;
    }
    .page-play .song li input {
        width: 8%;
        color: #fff;
        position: relative;
        top: 20px;
    }
    .page-play .song li div {
        width: 50%;
        color: #fff;
        position: relative;
        padding-left: 20px;
    }
    .page-play .song li div img {
        position: absolute;
        left: 0;
        top: 20px;
    }
    .page-play .song li p {
        width: 20%;
        color: #fff;
    }
    .page-play .song li span {
        color: #fff;
    }
</style>





