<template>
  <div class="page-home">
    <div class="bg-box">

    </div>
    <div class="recommendation">
      <div class="title">
        <h3 class="t_cen">歌单推荐</h3>
      </div>
      <div class="nav main">
        <ul>
          <li><a class="active" href="#">为你推荐</a></li>
          <li><a href="#">网络歌曲</a></li>
          <li><a href="#">民谣</a></li>
          <li><a href="#">摇滚</a></li>
          <li><a href="#">情歌</a></li>
          <li><a href="#">经典</a></li>
        </ul>
      </div>
      <div class="content">
        <div class="swiper-container swiper-container666">
          <div class="swiper-wrapper">
              <div v-for="(v,i) in swiperList" :key="i" class="swiper-slide">
                <ul id="swiper-box">
                  <li v-for="(v1,i1) in v" :key="i1" >
                    <router-link :to="'/home/songplay/' + v1.song_id">
                      <div class="img-box">
                        <img :src="v1.cover | decodeImgUrl">
                        <div class="paly-icon"></div>
                      </div>
                      <p>{{v1.title}}</p>
                      <span>播放量：{{(v1.listen_num/10000).toFixed(1)}}万</span>
                    </router-link>
                  </li>
                </ul>
              </div>
          </div>
        <!-- 如果需要分页器 -->
          <div class="swiper-pagination swiper-pagination666"></div>
        <!-- 如果需要导航按钮 -->
        </div>
        <div class="swiper-button-prev swiper-button-prev666"></div>
        <div class="swiper-button-next swiper-button-next666"></div>
      </div>
    </div>
    <div class="recommendation">
      <div class="title">
        <h3 class="t_cen">新歌首发</h3>
      </div>
      <div class="nav main">
        <div class="paly-all">播放全部</div>
        <ul>
          <li><a class="active" href="#">内地</a></li>
          <li><a href="#">港台</a></li>
          <li><a href="#">欧美</a></li>
          <li><a href="#">日本</a></li>
          <li><a href="#">韩国</a></li>
        </ul>
      </div>
      <div class="content">
          <div class="swiper-container swiper-container333">
            <div class="swiper-wrapper">
                <div v-for="(v,i) in newSongList" :key="i" class="swiper-slide">
                  <ul id="songlists">
                    <li v-for="(v1,i1) in v" :key="i1">
                      <router-link :to='"/home/songplay/" + v1.mid'>
                        <div class="img-box"> 
                          <img :src='"https://y.gtimg.cn/music/photo_new/T002R90x90M000" + v1.album.mid + ".jpg?max_age=2592000"'>
                          <div class="paly-icon"></div>
                        </div>  
                        <div>
                          <p>{{v1.album.title + " " + v1.album.subtitle}}</p>
                          <p>{{ v1.singer[0].name}}</p>
                        </div>
                        <span>03:57</span>
                      </router-link>  
                    </li>
                  </ul>
                </div>
            </div>
            <!-- 如果需要分页器 -->
          <div class="swiper-pagination swiper-pagination333"></div>
        </div>
        <div class="swiper-button-prev swiper-button-prev333"></div>
        <div class="swiper-button-next swiper-button-next333"></div>
      </div>
    </div>  
  </div>
</template>

<script>

import "@/assets/swiper-3.4.2.min"


if(!window.recom7059222676652006){
      window.recom7059222676652006 = function( info ){
        if( window.prevData === info ){
          window.prevData = false;
        }
        window.prevData = info;
      }
    }

import $ from "jquery"

export default {
  name: 'HelloWorld',
  data () {
    return {
      swiperList: [],
      newSongList: []
    }
  },
  created(){

    var _that = this;

       $.ajax({
        url: 'https://u.y.qq.com/cgi-bin/musicu.fcg',
        data: {
          g_tk: 5381,
          loginUin: 0,
          hostUin: 0,
          format: 'jsonp',
          inCharset: 'utf8',
          outCharset: 'utf-8',
          notice: 0,
          platform: 'yqq',
          needNewCode: 0,
          data: '{"comm":{"ct":24},"category":{"method":"get_hot_category","param":{"qq":""},"module":"music.web_category_svr"},"recomPlaylist":{"method":"get_hot_recommend","param":{"async":1,"cmd":2},"module":"playlist.HotRecommendServer"},"playlist":{"method":"get_playlist_by_category","param":{"id":8,"curPage":1,"size":40,"order":5,"titleid":8},"module":"playlist.PlayListPlazaServer"},"new_song":{"module":"QQMusic.MusichallServer","method":"GetNewSong","param":{"type":0}},"new_album":{"module":"music.web_album_library","method":"get_album_by_tags","param":{"area":1,"company":-1,"genre":-1,"type":-1,"year":-1,"sort":2,"get_tags":1,"sin":0,"num":40,"click_albumid":0}},"toplist":{"module":"music.web_toplist_svr","method":"get_toplist_index","param":{}},"focus":{"module":"QQMusic.MusichallServer","method":"GetFocus","param":{}}}'
        },
        dataType: 'JSONP',
        success: function( info ){
          // console.log( info )

          // 歌单推荐
          var arr = info.recomPlaylist.data.v_hot;
          _that.swiperList.push( arr.splice(0, 5) );
          _that.swiperList.push( arr.splice(0, 5) );

          // 新歌列表
          var arr = info.new_song.data.song_list;

          for( var i = 0; i < 4; i++ ){
              _that.newSongList.push( arr.splice(0, 9) )
          }

          // console.log( _that.newSongList )
          
        }

       })
          

  },
  mounted(){
    
  },
  updated(){
    this.setSwiper(); 
  },
  methods: {
    setSwiper(){
      var mySwiper = new Swiper ('.swiper-container666', {
        loop: true,
        // 如果需要分页器
        pagination: '.swiper-pagination666',
        // 如果需要前进后退按钮
        nextButton: '.swiper-button-next666',
        prevButton: '.swiper-button-prev666',
      })  
      var mySwiper2 = new Swiper ('.swiper-container333', {
        loop: true,
        // 如果需要分页器
        pagination: '.swiper-pagination333',
        // 如果需要前进后退按钮
        nextButton: '.swiper-button-next333',
        prevButton: '.swiper-button-prev333',
      })  
    }
  },
  filters: {
    decodeImgUrl( str ){
      return (str.split('600')[0] + 300);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.page-home {
  background-color: #fff;
}
.page-home,
.page-home .recommendation {
  position: relative;
}
.page-home .bg-box {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 280px;
  background-color: #F4F4F4;
}
.page-home .recommendation .title {
  padding: 50px 0 24px;
  font-size: 28px;
}
.page-home .recommendation .nav  {
  position: relative;
}
.page-home .recommendation .nav ul {
  text-align: center;
}
.page-home .recommendation .nav .paly-all {
  position: absolute;
  left: 0;
  top: -10px;
  border: 1px solid #ccc;
  width: 128px;
  height: 38px;
  line-height: 38px;
  text-align: center;
  font-size: 12px;
  transition: all .3s;
  cursor: pointer;
}
.page-home .recommendation .nav .paly-all:hover {
  background-color: #F4F4F4;
}
.page-home .recommendation .nav li {
  display: inline-block;
}
.page-home .recommendation .nav li a {
  padding: 0 30px;
}
.page-home .recommendation .nav li a.active,
.page-home .recommendation .nav li a:hover {
  color: #31c27c;
}
.page-home .recommendation .content {
  width: 1200px;
  margin: 0 auto;
  margin-top: 30px;
}

#swiper-box {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

#swiper-box li {
  width: 224px;
  padding-bottom: 70px;
}

#swiper-box li p {
  padding-top: 10px;
  font-size: 14px;
}
#swiper-box li span {
  font-size: 12px;
  color: #666;
}

#swiper-box li .img-box {
  width: 224px;
  height: 224px;
  background-color: pink;
  position: relative;
  overflow: hidden;
}
.paly-icon {
  transition: all 0.8s;
  opacity: 0;
  border-radius: 50%;
  background-color: #fff;
  position: absolute;
}
#swiper-box .img-box .paly-icon {
  width: 40px;
  height: 40px;
  left: 50%;
  top: 50%;
  margin-left: -20px;
  margin-top: -20px;
}

.img-box:hover .paly-icon {
  transform: scale(1.3);
  opacity: 1;
}
.img-box:hover img {
  transform: scale(1.05)
}

.paly-icon:after {
  content: '';
  position: absolute;
  height: 0;
  width: 0;
}

#swiper-box .img-box .paly-icon:after {
  left: 16px;
  top: 12px;
  border-bottom: 8px solid transparent;
  border-left: 14px solid #666;
  border-top: 8px solid transparent;
}

#swiper-box li .img-box img {
  width: 100%;
  height: 100%;
  display: block;
  transition: all 1s;
}

.swiper-container-horizontal>.swiper-pagination-bullets, 
.swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 25px;
}

.page-home #songlists {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 70px;
}
.page-home #songlists li {
  width: 31%;
  border-bottom: 1px solid #ddd;
  padding: 15px 0;
}
#songlists img {
  width: 100%;
  height: 100%;
  display: block;
  transition: all 1s;
}
#songlists .img-box {
  float: left;
  width: 86px;
  height: 86px;
  padding: 0;
  position: relative;
  overflow: hidden;
}
#songlists .img-box .paly-icon {
  width: 16px;
  height: 16px;
  left: 50%;
  top: 50%;
  margin-left: -16px;
  margin-top: -16px;
}
#songlists div {
  float: left;
  width: 210px;
  padding-top: 20px;
  padding-left: 20px;
  cursor: pointer;
}

#songlists .paly-icon:after {
  left: 14px;
  top: 10px;
  border-bottom: 8px solid transparent;
  border-left: 14px solid #666;
  border-top: 8px solid transparent;
}

#songlists div p {
  overflow: hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
  font-size: 14px;
}

#songlists div p:last-child {
  color: #999;
}
#songlists div p:hover {
  color: #31c27c;
}
#songlists span {
  float: right;
  color: #999;
  padding-top: 40px;
}


</style>



