import Vue from 'vue'
import App from './App'
import router from './router'

import './assets/swiper-3.4.2.min.css'

Vue.config.productionTip = false

import Vuex from 'vuex'

Vue.use(Vuex)

function setStorge( data ){
  localStorage.setItem( 'mySongLists', JSON.stringify(data) )
}

function getStorge(){
  return JSON.parse( localStorage.getItem('mySongLists') );
}

const store = new Vuex.Store({
  state: {
    songLists: getStorge() || [],
    usePlay: false
  },
  mutations: {
    increment (state, data) {
      console.log( 'increment触发了' , data);
      var flag = state.songLists.find( function(v){
        return v.id == data.id;
      })
      console.log( flag )
      if( !flag ){
        state.songLists.unshift( data );
      }
      state.usePlay = !state.usePlay; 
    }
  },
  getters:{
    allLists: function(state){
        return state.songLists;
    },
    isPlay: function(state){
        return state.usePlay
    }
}

})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App),
  store
})



